import React, { Component } from 'react'

class ColumnTally extends Component {
  render() {
    const cart = this.props.cart
    const itemsToRender = cart.map(cartItem => cartItem.confirmationStatus)
    const filt = itemsToRender.length

    return (
      <div>
        <p>{filt} of</p>
        {/* {cart.map((cartItem, index) => {
          return <p key={index}>{cartItem.name}</p>
        })} */}
      </div>
    )
  }
}

export default ColumnTally
